.lang-switcher {
   position: relative;
   display: flex;
   column-gap: 1.4rem;
   align-items: flex-start;
   color: var(--primary-color);

   &:focus-within {
      outline: 4px solid #b25200;
      outline-offset: 1px;
   }
}

.lang-switcher__btn {
   width: 4.1rem;
   height: 2.73rem;

   img {
      width: 100%;
      height: 100%;
      object-fit: cover;
   }
}

.lang-switcher__icon {
   display: block;
   width: 1.1rem;
   height: 0.627rem;
   margin-top: 1.2rem;
   color: var(--primary-color);

   svg {
      width: 100%;
      height: 100%;
      color: inherit;
      fill: currentcolor;
      object-fit: cover;
   }
}